

















import Vue from 'vue';
import { Component, Prop, Watch } from 'vue-property-decorator';

@Component
export default class extends Vue {
  @Prop({
    type: Array,
    default: () => {
      return []
    }
  })
  colorArr: any;

  @Prop()
  name: string;

  @Prop()
  title: string;

  get middleIndex():number {
    return Math.ceil(this.colorArr.length / 2);
  }
}



