





































import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';

import XPanel from '@/components/panel.vue';
import XApiTable from '@/components/api-table.vue';

@Component({
  components: {
    XApiTable,
    XPanel
  }
})
export default class extends Vue {
  @Prop({default: ''})
  className: string;

  @Prop({ default: '' })
  title: string;

  @Prop({ default: '' })
  des: string;

  @Prop({ 
    type: Array,
    default: () => {
      return [];
    }  
  })
  card: any[];

  @Prop({ 
    type: Object, 
    default: () => {
      return {}
    } 
  })
  api: any;
}



