







































import Vue from 'vue';
import { Component } from 'vue-property-decorator';

import { Chrome } from 'vue-color';
import * as colorMix from '@/utils/color-mix';
import XColorItem from '@/components/color-item.vue';

const baseColor = {
  primary: {
    name: '主色调（Primary）',
    hex: '#14954B'
  },
  forest: {
    name: '森林绿（Forest）',
    hex: '#14954B'
  },
  ocean: {
    name: '海洋蓝（Ocean）',
    hex: '#177ED7'
  },
  rose: {
    name: '玫瑰红（Rose）',
    hex: '#E80404'
  },
  flame: {
    name: '烈焰橙（Flame）',
    hex: '#F46300'
  },
  bud: {
    name: '新芽绿（Bud）',
    hex: '#82A01C'
  },
  lemon: {
    name: '柠檬黄（Lemon）',
    hex: '#FDB500'
  },
  violet: {
    name: '罗兰紫（Violet）',
    hex: '#744EA1'
  },
  sakura: {
    name: '樱花粉（Sakura）',
    hex: '#DD3E8F'
  },
  gray: {
    name: '中性灰（Gray）',
    hex: '808080'
  }
};

const palette = [
  {
    type: 'tint',
    key: '50',
    value: 0.88
  },
  {
    type: 'tint',
    key: '100',
    value: 0.64
  },
  {
    type: 'tint',
    key: '200',
    value: 0.48
  },
  {
    type: 'tint',
    key: '300',
    value: 0.32
  },
  {
    type: 'tint',
    key: '400',
    value: 0.16
  },
  {
    type: 'tint',
    key: '500',
    value: 0
  },
  {
    type: 'shade',
    key: '600',
    value: 0.16
  },
  {
    type: 'shade',
    key: '700',
    value: 0.32
  },
  {
    type: 'shade',
    key: '800',
    value: 0.48
  },
  {
    type: 'shade',
    key: '900',
    value: 0.64
  },
]

@Component({
  components: {
    'chrome-picker': Chrome,
    XColorItem
  }
})
export default class extends Vue {
  baseColor: any = baseColor;
  colors: any = {};
  
  beforeCreate () {
    this.$nextTick().then(() => {
      console.log(2122)
    })
  }
  created() {
    for (let color in baseColor) {
      this.$set(this.colors, color, this.colorMix(baseColor[color].hex));
    }

    this.$nextTick().then(() => {
      console.log(222)
    })
  }

  updateValue(color) {
    this.$set(this.colors, 'primary', this.colorMix(color.hex));
  }
  
  colorMix(hex) {
    const colors = [];
    palette.map((item) => {
      let color = colorMix[item.type](hex.replace('#', ''), item.value).toUpperCase();
      colors.push({
        key: item.key,
        color: color
      });
    });
    return colors;
  }
}



